.contenido-temas{
    padding-bottom: 5rem;
    div{
        margin-bottom: 3rem;
        height: 30rem;
        background-repeat: no-repeat;
        background-position: center center;
        display: flex;
        justify-content: start;
        align-items: center;
        padding: 3rem;
        position: relative;
        

        p{
            font-size: 2.5rem;
            text-transform: uppercase;
            z-index: 4;
            color: white;
            font-weight: 500;
        }

        .sombra-contenido{
            position: absolute;
            content: '';
            background-color: rgba(10, 10, 10, 0.658);
            top: 0;
            left: 0;
            right: 0;
            bottom: 0;
            width: 100%;
            z-index: 2;
        }
    }

    .button-leer{
        position: absolute;
        bottom: 10px;
        right: 20px;
        font-size: 2rem;
        color: white;
        border: none;
        z-index: 4;
        background: none;
        text-transform: uppercase;
        border: 1px solid rgb(192, 181, 181);
        padding: .5rem 1rem;


        &:hover{
            transition: .3s all ease-in-out;
            transform: scale(1.1);
            cursor: pointer;
        }
    }

    @include tablet{
        div{

            p{
                font-size: 3.5rem;
            }
        }

        .button-leer{
            font-size: 3rem;
            padding: 1rem 1.5rem;
        }
    }
}
.contenido-henry-dice{
    display: flex;
    flex-direction: column;
}

.contenido-henry-dice > div{
    width: 50%;

}
.frase-henry{
    position: absolute;
    margin: 0 auto;
    top: 70%;

    span{
        font-size: 3rem;
        color: rgb(43, 40, 40);
        text-transform: uppercase;
        text-align: justify;
    }
}
.henryDice{
    img{
        width: 30rem;
        margin-bottom: 8rem;
    }
}


@include tablet{
    .contenido-henry-dice{
        flex-direction: row;
    }

    .frase-henry{
        position: inherit;
        display: flex;
        justify-content: center;
        align-items: center;

        span{
            font-size: 5rem;
            font-weight: 500;
        }
    }
}

.contenido-proposito{
    margin-bottom: 5rem;

    div{
        padding: 1rem;
        margin-bottom: 1rem;
        border-radius: 1rem;
        position: relative;

        &:hover{
            transition: .3s all ease-in-out;
            transform: scale(1.1);
            z-index: 8;
        }
    }

    p{
        font-size: 1.5rem;
    }
    .number{
        position: absolute;
        bottom: -50px;
        right: -15px;
        font-size: 2rem;
        padding: 3rem;
        border-radius: 50%;
        box-shadow: -10px 0 140px rgb(31, 29, 29);
        width: 2rem;
        height: 2rem;
        z-index: 6;
    }

    @include tablet{

        div{
            padding: 2rem;
            margin-bottom: 2rem;
        }
        p{
            font-size: 3rem;
            color: white;
        }

        .number{
            font-size: 4rem;
            width: 8rem;
            height: 8rem;
        }
       
    }
}