.navegacion-mobile{
    position: fixed;
    top: 0; 
    background-color: rgb(239, 247, 238);
    height: 100vh;
    padding: 3rem;
    z-index: 7;
    visibility: hidden;
    opacity: 0;
    transition: .3s all ease-in-out;
    box-shadow: -10px 0 140px rgb(31, 29, 29);
    display: flex;
    justify-content: center;
    align-items: center;

}

.show-navMobile{
    
    visibility: visible;
    opacity: 1;
}
.icono-light{
    position: absolute;
    top: 50px;
    right: 100px;
    width: 4rem
   
}

.squine{
    
    position: fixed;
    top: 5px;
    left: 160px;
    z-index: 9;
    width: 4rem;

    
}

.iconos-link-mobile{
    .iconos-link{
   
        a{
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
        }
        img{
            width: 3rem;
        }

        p{
            font-size: 1.5rem;
            font-weight: bold;
            font-style: normal;
        }
    }
}

@include tablet{

    .navegacion-mobile{
        display: none;
    }

    @include tablet{
        .icono-light{
            width: 3rem;
            margin: 0 2rem;
            position: inherit;
        }
    }
}