html {
  font-size: 62.5%;
  box-sizing: border-box;
}

body {
  font-family: "Hubballi", cursive;
  color: rgb(17, 16, 16);
}

*, *:before, *:after {
  box-sizing: inherit;
}

a {
  text-decoration: none;
  color: rgb(22, 21, 21);
}

img {
  padding: 0;
  width: 100%;
}

.contenedor {
  max-width: 115rem;
  width: 90%;
  margin: 0 auto;
}

.barra-desk {
  padding: 2rem 0;
  display: flex;
  justify-content: space-between;
}
.barra-desk .iconos-nav {
  width: 15rem;
  display: flex;
  flex-direction: row;
}
.barra-desk .iconos-nav img {
  object-fit: cover;
  width: 100%;
}

.navegacion-desk {
  display: none;
}

.menu-contenedor {
  display: flex;
  align-items: center;
  padding: 2rem;
}

@media (min-width: 700px) {
  .menu-contenedor {
    display: none;
  }

  .contenedor-nav-desk {
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .navegacion-desk {
    display: flex;
    gap: 3rem;
  }
  .navegacion-desk a {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  .navegacion-desk img {
    width: 2rem;
  }
  .navegacion-desk p {
    margin-top: 0;
    font-weight: 600;
    font-size: 1.4rem;
    text-transform: uppercase;
  }

  .barra-desk {
    padding: 0;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }

  .iconos-nav {
    display: flex;
    margin-bottom: 1.5rem;
  }
}
@media (min-width: 1024px) {
  .barra-desk {
    padding: 2rem 0;
    flex-direction: row;
    justify-content: space-between;
    align-items: inherit;
  }

  .iconos-nav {
    margin-bottom: 0;
  }
}
.contenido-header {
  background-image: url(/build/img/grow-g96dc01bff_1920.webp);
  background-size: cover;
  position: relative;
  background-repeat: no-repeat;
  background-position: center center;
  width: auto;
  height: 50rem;
  display: flex;
  justify-content: center;
  align-items: center;
}
.contenido-header .letter-title {
  color: white;
  font-size: 5rem;
  text-transform: uppercase;
  z-index: 5;
  max-width: 40rem;
  font-weight: normal;
}

.contenido-header::before {
  background-color: rgba(14, 13, 13, 0.651);
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  width: 100%;
  z-index: 3;
  content: "";
}

.not-header {
  background-color: rgb(243, 241, 241);
}

.navegacion-mobile {
  position: fixed;
  top: 0;
  background-color: rgb(239, 247, 238);
  height: 100vh;
  padding: 3rem;
  z-index: 7;
  visibility: hidden;
  opacity: 0;
  transition: 0.3s all ease-in-out;
  box-shadow: -10px 0 140px rgb(31, 29, 29);
  display: flex;
  justify-content: center;
  align-items: center;
}

.show-navMobile {
  visibility: visible;
  opacity: 1;
}

.icono-light {
  position: absolute;
  top: 50px;
  right: 100px;
  width: 4rem;
}

.squine {
  position: fixed;
  top: 5px;
  left: 160px;
  z-index: 9;
  width: 4rem;
}

.iconos-link-mobile .iconos-link a {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.iconos-link-mobile .iconos-link img {
  width: 3rem;
}
.iconos-link-mobile .iconos-link p {
  font-size: 1.5rem;
  font-weight: bold;
  font-style: normal;
}

@media (min-width: 700px) {
  .navegacion-mobile {
    display: none;
  }
}
@media (min-width: 700px) and (min-width: 700px) {
  .icono-light {
    width: 3rem;
    margin: 0 2rem;
    position: inherit;
  }
}
.mod-dark-add {
  transition: 0.3s all ease-in-out;
  background-color: rgb(20, 20, 20);
}
.mod-dark-add p, .mod-dark-add h1, .mod-dark-add h2, .mod-dark-add h3, .mod-dark-add label, .mod-dark-add legend, .mod-dark-add span, .mod-dark-add a {
  color: white;
}
.mod-dark-add .navegacion-mobile img, .mod-dark-add .iconos-link img, .mod-dark-add .icono-light img, .mod-dark-add .menu-contenedor img {
  filter: invert(100%);
}
.mod-dark-add .navegacion-mobile {
  background-color: rgb(43, 38, 38);
}
.mod-dark-add .not-header, .mod-dark-add .footer-section {
  background-color: rgb(24, 24, 24);
}
.mod-dark-add .contain-extra {
  background-color: rgba(72, 75, 75, 0.301);
}

.information-main .title-section {
  text-align: center;
  font-size: 3rem;
  text-transform: uppercase;
}

.informacion-alumnos, .contain-certifaid {
  margin-top: 4rem;
}
.informacion-alumnos p, .contain-certifaid p {
  text-align: justify;
  font-size: 2.5rem;
  padding: 2rem;
}

.contenido-calendario-ambiental p {
  text-align: center;
  font-size: 2rem;
}

.contenido-alumnos {
  display: flex;
  gap: 3rem;
  flex-direction: row;
  flex-flow: wrap;
  padding: 2rem;
}
.contenido-alumnos div {
  transition: 0.3s;
  width: 20%;
  margin: 0 auto;
}
.contenido-alumnos div h2 {
  text-align: center;
}
.contenido-alumnos div:hover {
  transform: scale(1.05);
  box-shadow: 0 0 40px -10px rgba(0, 0, 0, 0.25);
}

.body {
  position: relative;
}

.contain-certifaid .contenedor-hijo {
  padding: 4rem 3rem;
}
.contain-certifaid .contenedor-hijo img {
  margin-bottom: 2rem;
}
@media (min-width: 700px) {
  .contain-certifaid .contenedor-hijo {
    padding: 4rem 0;
    display: flex;
    justify-content: space-around;
  }
  .contain-certifaid .contenedor-hijo img {
    width: 40rem;
    transition: 0.3s;
  }
  .contain-certifaid .contenedor-hijo img:hover {
    transform: scale(1.05);
  }
}

.containMaster {
  padding: 2rem;
  margin-top: 7rem;
}
.containMaster h2 {
  font-size: 2.5rem;
  text-transform: uppercase;
}

.contenido-calendario-ambiental .legend {
  font-size: 3rem;
}

.contain-extra {
  background-color: rgba(19, 20, 20, 0.116);
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  padding: 2rem;
  width: 100%;
}
.contain-extra .contain-child {
  border: 0.3px solid rgb(122, 113, 113);
  padding: 0.5rem;
}

@media (min-width: 700px) {
  .containMaster {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 1rem;
  }
}
@media (min-width: 1024px) {
  .contain-extra {
    grid-template-columns: repeat(3, 1fr);
  }
}
.footer-section {
  background-color: #f3f1f1;
  padding: 3rem;
}
.footer-section p {
  font-size: 1.7rem;
  text-transform: uppercase;
  text-align: center;
  font-weight: 600;
}
.footer-section .enlaces-footer {
  display: flex;
  flex-direction: column;
  text-align: center;
}
@media (min-width: 700px) {
  .footer-section .enlaces-footer {
    flex-direction: row;
    margin-top: 4rem;
    text-align: start;
    gap: 3rem;
  }
}

.title-mascota {
  font-size: 3rem;
  text-transform: uppercase;
  text-align: center;
}

.information {
  display: flex;
  justify-content: center;
  width: 100%;
  flex-direction: column;
}
.information .contenido-info p {
  font-size: 2.5rem;
  line-height: 3rem;
  text-align: justify;
}

.reverse-column2 {
  flex-direction: column-reverse;
}

.reverse-column1, .reverse-column2 {
  display: flex;
  justify-content: center;
  align-items: center;
}

.reverse-column1 img, .reverse-column2 img {
  width: 25rem;
}

@media (min-width: 700px) {
  .information {
    flex-direction: row;
  }

  .img-info {
    width: 30%;
  }

  .contenido-info {
    width: 80%;
  }
}
.contenido-temas {
  padding-bottom: 5rem;
}
.contenido-temas div {
  margin-bottom: 3rem;
  height: 30rem;
  background-repeat: no-repeat;
  background-position: center center;
  display: flex;
  justify-content: start;
  align-items: center;
  padding: 3rem;
  position: relative;
}
.contenido-temas div p {
  font-size: 2.5rem;
  text-transform: uppercase;
  z-index: 4;
  color: white;
  font-weight: 500;
}
.contenido-temas div .sombra-contenido {
  position: absolute;
  content: "";
  background-color: rgba(10, 10, 10, 0.658);
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  z-index: 2;
}
.contenido-temas .button-leer {
  position: absolute;
  bottom: 10px;
  right: 20px;
  font-size: 2rem;
  color: white;
  border: none;
  z-index: 4;
  background: none;
  text-transform: uppercase;
  border: 1px solid rgb(192, 181, 181);
  padding: 0.5rem 1rem;
}
.contenido-temas .button-leer:hover {
  transition: 0.3s all ease-in-out;
  transform: scale(1.1);
  cursor: pointer;
}
@media (min-width: 700px) {
  .contenido-temas div p {
    font-size: 3.5rem;
  }
  .contenido-temas .button-leer {
    font-size: 3rem;
    padding: 1rem 1.5rem;
  }
}

.contenido-henry-dice {
  display: flex;
  flex-direction: column;
}

.contenido-henry-dice > div {
  width: 50%;
}

.frase-henry {
  position: absolute;
  margin: 0 auto;
  top: 70%;
}
.frase-henry span {
  font-size: 3rem;
  color: rgb(43, 40, 40);
  text-transform: uppercase;
  text-align: justify;
}

.henryDice img {
  width: 30rem;
  margin-bottom: 8rem;
}

@media (min-width: 700px) {
  .contenido-henry-dice {
    flex-direction: row;
  }

  .frase-henry {
    position: inherit;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .frase-henry span {
    font-size: 5rem;
    font-weight: 500;
  }
}
.contenido-proposito {
  margin-bottom: 5rem;
}
.contenido-proposito div {
  padding: 1rem;
  margin-bottom: 1rem;
  border-radius: 1rem;
  position: relative;
}
.contenido-proposito div:hover {
  transition: 0.3s all ease-in-out;
  transform: scale(1.1);
  z-index: 8;
}
.contenido-proposito p {
  font-size: 1.5rem;
}
.contenido-proposito .number {
  position: absolute;
  bottom: -50px;
  right: -15px;
  font-size: 2rem;
  padding: 3rem;
  border-radius: 50%;
  box-shadow: -10px 0 140px rgb(31, 29, 29);
  width: 2rem;
  height: 2rem;
  z-index: 6;
}
@media (min-width: 700px) {
  .contenido-proposito div {
    padding: 2rem;
    margin-bottom: 2rem;
  }
  .contenido-proposito p {
    font-size: 3rem;
    color: white;
  }
  .contenido-proposito .number {
    font-size: 4rem;
    width: 8rem;
    height: 8rem;
  }
}

.level1 {
  margin: 8rem 0;
}
.level1 .legend-frase h2, .level1 .legend-frase p {
  text-align: center;
}
.level1 .legend-frase h2 {
  font-size: 3.5rem;
  text-transform: uppercase;
  position: relative;
}
.level1 .legend-frase h2:after {
  content: "";
  background-color: white;
  display: block;
  position: absolute;
  left: 30px;
  right: -50px;
  height: 0.5rem;
  top: 50px;
}
.level1 .legend-frase p {
  font-size: 2.5rem;
}
@media (min-width: 1024px) {
  .level1 .legend-frase {
    display: flex;
    justify-content: center;
    align-items: center;
    column-gap: 3rem;
  }
  .level1 .legend-frase h2:after {
    width: 0;
  }
  .level1 .legend-frase h2 {
    font-size: 4rem;
    text-transform: uppercase;
  }
  .level1 .legend-frase h2:before {
    content: "";
    background-color: white;
    width: 0.3rem;
    height: 8rem;
    display: block;
    position: absolute;
    left: none;
    right: -20px;
    top: -15px;
  }
  .level1 .legend-frase p {
    font-size: 2.8rem;
  }
}