
.title-mascota{
    font-size: 3rem;
    text-transform: uppercase;
    text-align: center;
}
.information{
    display: flex;
    justify-content: center;
    width: 100%;
    flex-direction: column;


    .contenido-info{

        p{
            font-size: 2.5rem;
            line-height: 3rem;
            text-align: justify;
        }
    }
   

    
}

.reverse-column2{
    flex-direction: column-reverse;
}
.reverse-column1, .reverse-column2{
    display: flex;
    justify-content: center;
    align-items: center;
}
.reverse-column1 img, .reverse-column2 img{
    width: 25rem;

}

@include tablet{
    .information{
        flex-direction: row;
    }
    .img-info{
        width: 30%;
    }
    .contenido-info{
        width: 80%;
    }
}