.barra-desk{
    padding: 2rem 0 ;
    display: flex;
    justify-content: space-between;

    .iconos-nav{
        width: 15rem;
        
        display: flex;
        flex-direction: row;


        img{
            object-fit: cover;
            width: 100%;

        }
    }   

  

}
.navegacion-desk{
    display: none;
}

.menu-contenedor{
    display: flex;
    align-items: center;
    padding: 2rem;
}
@include tablet{

    .menu-contenedor{
        display: none;
    }
    .contenedor-nav-desk{
        display: flex;
        justify-content: center;
        align-items: center;
    }
    .navegacion-desk{

        display: flex;
        gap: 3rem;

        a{
            display: flex;
           
            flex-direction: column;
            align-items: center;
            
        }
        img{
            width: 2rem;
           
        }

        p{
  
            margin-top: 0;
            font-weight: 600;
            font-size: 1.4rem;
            text-transform: uppercase;
        }
    }

    .barra-desk{
        padding: 0;
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }

    .iconos-nav{
        display: flex;
        margin-bottom: 1.5rem;
    }

}

@include desktop{
    .barra-desk{
        padding: 2rem 0;
        flex-direction: row;
        justify-content: space-between;
        align-items: inherit;
    }
    .iconos-nav{
        margin-bottom: 0;
    }
}

