.footer-section{
    background-color: #f3f1f1;
    padding: 3rem;

    p{
        font-size: 1.7rem;
        text-transform: uppercase;
        text-align: center;
        font-weight: 600;
    }

    .enlaces-footer{
        display: flex;
        flex-direction: column;
        text-align: center;
       
    }
    @include tablet{
        .enlaces-footer{
    
            flex-direction: row;
            margin-top: 4rem;
            text-align: start;
            gap: 3rem;
        }
    }
}

