
.body{
    position: relative;
}
.contain-certifaid{
    .contenedor-hijo{
        padding: 4rem 3rem;

        img{
            margin-bottom: 2rem;
        }
    }

    @include tablet{
        .contenedor-hijo{
            padding: 4rem 0;
            display: flex;
            justify-content: space-around;
            img{
                width: 40rem;
                transition: 0.3s;
    
                &:hover{
                    transform: scale(1.05);
                    
                }
            }
    
    
        }
    }
    
}
