html {
    font-size: 62.5%;
    box-sizing: border-box;
}
body{
    font-family: $hubballi;
    color: rgb(17, 16, 16);
}
*, *:before, *:after {
    box-sizing: inherit;
}

a{
    text-decoration: none;
    color: rgb(22, 21, 21)
}

img{
    padding: 0;
    width: 100%;
}

.contenedor{
    max-width: 115rem;
    width: 90%;
    margin: 0 auto;
}