.mod-dark-add{
    transition: .3s all ease-in-out;
    background-color: rgb(20, 20, 20);

    p, h1, h2, h3, label, legend, span, a{
        color: white;
    }

    .navegacion-mobile, .iconos-link, .icono-light, .menu-contenedor{
        
        img{
            filter: invert(100%);
        }
       
    }
    .navegacion-mobile{
        background-color: rgb(43, 38, 38);
    }

    .not-header, .footer-section{
        background-color: rgb(24, 24, 24);
    }

 
    .contain-extra{
        background-color: rgba(72, 75, 75, 0.301);
    }


   
  
    
}