.containMaster{
    padding: 2rem;
    margin-top: 7rem;
    h2{
       
        font-size: 2.5rem;
        text-transform: uppercase;
    }
}
.contenido-calendario-ambiental .legend{
    font-size: 3rem;
}


.contain-extra{
    background-color: rgba(19, 20, 20, 0.116);
    display:grid;
    grid-template-columns: repeat(2, 1fr);
    padding: 2rem;
    width: 100%;
    .contain-child{

        border: .3px solid rgb(122, 113, 113);
        padding: .5rem;
     
    }

}

@include tablet{
    .containMaster{
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        gap: 1rem;
    }


}

@include desktop{

    .contain-extra{
        grid-template-columns: repeat(3, 1fr);
    }
}