
.level1{
    margin: 8rem 0;
   
    .legend-frase{

        h2, p{
            text-align: center;
        }
        h2{
            font-size: 3.5rem;
            text-transform: uppercase;
            position: relative;

            &:after{
                content: '';
                background-color: white;
                display: block;
                position: absolute;
                left: 30px;
                right: -50px;
                height: .5rem;
                top: 50px;
            }
        }
        p{
            font-size: 2.5rem;
        }

        @include desktop{
            display: flex;
            justify-content: center;
            align-items: center;
            column-gap: 3rem;

            h2{

                &:after{
                    
                    width: 0;
                }
            }

            h2{
                font-size: 4rem;
                text-transform: uppercase;
           
    
                &:before{
                    content: '';
                    background-color: white;
                    width: .3rem;
                    height: 8rem;
                    display: block;
                    position: absolute;
                    left: none;
                    right: -20px;
                    top: -15px;
                }
            }
            p{
                font-size: 2.8rem;
            }

        }
        
       

        
    }

}