.contenido-header{
    background-image: url(/build/img/grow-g96dc01bff_1920.webp);
    background-size: cover;
    position: relative;
    background-repeat: no-repeat;
    background-position: center center;
    width: auto;
    height: 50rem;
    display: flex;
    justify-content: center;
    align-items: center;
 

    .letter-title{
        color: white;
        font-size: 5rem;
        text-transform: uppercase;
        z-index: 5;
        max-width: 40rem;

        font-weight: normal;

        
     
    }
    
}

.contenido-header::before{
    background-color: rgba(14, 13, 13, 0.651);
    position: absolute;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
    width: 100%;
    z-index: 3;
    content: '';
}

.not-header{
    background-color: rgb(243, 241, 241);
}